@use '../helpers' as *;
@use '../components/burger' as *;
@use '../components/corner' as *;
@use '../animated/back_entrances/backInRight' as *;
@use '../animated/back_exits/backOutRight' as *;
@use '../animated/fading_exits/fadeOut' as *;
@use '../animated/sliding_entrances/slideInDown' as *;
@use '../animated/fading_entrances/fadeInBottomRight' as *;
@use '../animated/fading_exits/fadeOutBottomRight' as *;
@use '../animated/attention_seekers/jello';


header {
    display: flex;

    justify-content: flex-end;
    height: 60px;

    margin: {
        top: 40px;
        bottom: 42px;
    }

    color: $header-color;
    background: $header-bg-color;
    user-select: none;


    a {
        color: $header-color;

        &:hover,
        &:active {
            color: $header-hover-color;
        }
    }



    .logo {
        margin-right: auto;
        height: 44px;
        line-height: 44px;
        overflow: hidden;

        &:hover {
            @extend %jello;
            animation-iteration-count: infinite;
            animation-duration: 1.2s;
        }



    }

    .nav-btn {
        @include sizer(40px);

        @include md {
            display: none;
        }
    }
}



body:not(.menu-shown) .nav {
    display: none;

    @include font-size(17px, $line-height: 21px);
    text-align: center;
    justify-content: flex-end;
    align-items: flex-start;

    &>a {
        padding: .75rem 0;
        margin-right: 2rem;
        position: relative;

        &:last-child {
            margin-right: 0;
        }

        &:before {
            position: absolute;
            content: '';
            bottom: .25rem;
            left: 50%;
            right: 50%;
            height: 3px;
            background: $header-color;
            transition: .75s;

        }

        &:hover,
        &:active {
            font-weight: 700;
            text-shadow: $box-shadow;

            &:before {
                left: 25%;
                right: 25%;
            }
        }

    }



    @include md {
        display: flex;
    }

}

.menu-shown {

    .logo {
        position: absolute;
        top: 40px;
        left: 5vw;
        z-index: 3;

        img {
            filter: invert(100%);
        }

        &:hover img {
            filter: invert(100%) contrast(100);

            //animation-duration: 200ms;
        }
    }

    .nav-btn {
        position: absolute;
        top: 40px;
        right: 5vw;
        z-index: 3;
        display: unset;
        color: $overlay-color;

        &:hover {
            color: $overlay-hover-color;
        }
    }

    .nav {
        @include overlay($position: fixed, $z-index: 2);


        background-color: $overlay-bg-color;
        @include font-size(40px, $line-height: 48px);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 50vh 25vw 5vh 5vw;

        @extend %slideInDown;
        animation-duration: 500ms;

        >a {
            color: $overlay-color;
            text-align: left;
            font-weight: 700;
            text-shadow: $box-shadow;
            @extend %backInRight;
            animation-duration: 500ms;
            animation-delay: 500ms;

            &:hover,
            &:active {
                font-size: 44px;
                color: $overlay-hover-color;
            }



            @for $i from 2 through 5 {
                &:nth-child(#{$i}) {
                    animation-delay: 475ms + ($i * 25ms);
                }
            }

        }
    }
}

.overlay {
    position: fixed;
    z-index: 104;
    bottom: 0;
    right: 0;
    width: 175px;
    height: 175px;

    @extend %fadeInBottomRight;
    animation-delay: 1s;
    animation-duration: 500ms;

}


.corner {
    @include corner(250px, $background-color: $corner-bg-color, $color: $corner-color);
    position: fixed;
}



.menu-closing {
    .nav {

        @extend %fadeOut;
        animation-delay: 800ms;
        animation-duration: 400ms;

        >a {
            @extend %backOutRight;
            animation-duration: 500ms;
            animation-delay: 300ms;

            @for $i from 2 through 5 {
                &:nth-child(#{$i}) {
                    animation-delay: 275ms + ($i * 25ms);
                }
            }

        }


    }

    .overlay {
        @extend %fadeOutBottomRight;

        animation-duration: 300ms;
    }
}





@include burger-btn-animation;